<template>
  <div id="column">
    <van-pull-refresh v-model="$store.state.column.refreshing" @refresh="onRefresh">
    <van-list
        v-model="$store.state.column.loading"
        :finished="$store.state.column.finished"
        finished-text="没有更多了"
        @load="onLoad"
    >
      <van-cell v-for="(item,index) in list" :key="index">
        <router-link :to='{name:"detail",params:{id:item._id},query:{id:item._id,toPath:"column"}}'>
          <h2>{{ item.title }}</h2>
          <p>{{ item.des }}</p>
        </router-link>
      </van-cell>
    </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import {mapActions,mapState} from "vuex";
import {throttle} from "lodash/function";
export default {
  name: "column",
  data(){
    return {
    }
  },
  methods: {
    ...mapActions('column',['column']),
    onLoad:throttle(function(){
      this.column({_page: parseInt(this.list.length / 14)})
    },600),
    onRefresh() {
      this.onLoad();
    },
  },
  computed:mapState('column',{
    list:state=>state.list
  })
}
</script>

